.vtimeline:before {
    content: "";
    position: absolute;
    height: 100%;
    width: 2px;
    background: #17497e;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: 0 auto
}

.vtimeline-point {
    position: relative;
    width: 100%;
    margin: -50px 0;
    min-height: 200px
}

.vtimeline-point:first-child {
    margin-top: 0
}

