    margin-bottom: 0
}

@media(max-width:767.98px) {
    .vtimeline-point {
        margin: 0 0 30px
    }
}

.vtimeline-icon {
    position: absolute;
    top: 50%;
    height: 35px;
    width: 35px;
    left: 0;
    right: 0;
    margin: 0 auto;
    border-radius: 50%;
    background: #35919c;
    border: 5px solid #fff
}

.vtimeline-block {
    width: 45%;
    margin: 0;
    transition: all .5s ease;
    z-index: 100;
    position: relative
}

.vtimeline-block:after {
    content: "";
    position: absolute;
    top: 50%;
    left: 45%;
    width: 0;
    height: 0;
    border: 10px solid transparent;
    border-right: none;
    border-left: 10px solid #eee
}

.vtimeline-right {
    margin-left: 55%
}

.vtimeline-right.vt-animate-slide .vtimeline-content {
    transform: translateX(100%)
}

.vtimeline-right .vtimeline-date {
    left: auto;
    right: 55%
}

.vtimeline-right:after {
    left: auto;
    right: 45%;
    border: 10px solid transparent;
    border-right-color: #eee;
    border-left: none
}

.vtimeline-date {
    position: absolute;
    left: 55%;
    top: 50%
}

.vtimeline-content {
    background: #eee;
    transition: all .5s ease
}

.vt-animate-fade,
.vt-animate-slide {
    opacity: 0
}

.vt-animate-slide .vtimeline-content {
    transform: translateX(-100%)
}

.vt-noarrows:after {
    content: none
}

@media only screen and (max-width:992px) {
    .vtimeline:before {
        left: 30px;
        right: auto;
        margin: 0
    }
    .vtimeline-icon {
        left: -10px;
        right: auto;
        margin: 0
    }
    .vtimeline-block {
        width: 85%;
        margin-left: 15%
    }
    .vtimeline-block:after {
        left: auto;
        right: 85%;
        border: 10px solid transparent;
        border-right-color: #eee;
        border-left: none
    }
    .vtimeline-date {
        position: relative;
        display: block;
        top: 0;
        left: 0;
        right: auto;
        background: #eee
    }
    .vtimeline-right .vtimeline-date {
        right: auto
    }
    .vtimeline.basic .vtimeline-date {
        padding: 10px 10px 0
    }
    .vt-animate-slide .vtimeline-content {
        transform: translateX(100%)
    }
}

